<template>
    <va-navbar class="app-navbar">
        <header-selector slot="selector" :minimized.sync="minimizedProxy"/>
    </va-navbar>
</template>

<script>
  import VaIconVuestic
    from 'vuestic-ui/src/components/vuestic-components/va-icon/va-iconset/VaIconVuestic'
  import HeaderSelector from './components/HeaderSelector'
  import LanguageDropdown from './components/dropdowns/LanguageDropdown'
  import ProfileDropdown from './components/dropdowns/ProfileDropdown'
  import NotificationDropdown from './components/dropdowns/NotificationDropdown'
  import MessageDropdown from './components/dropdowns/MessageDropdown'
  import SettingsDropdown from './components/dropdowns/SettingsDropdown'
  import ColorDropdown from './components/dropdowns/ColorDropdown'

  export default {
    name: 'app-navbar',
    components: {
      SettingsDropdown,
      ColorDropdown,
      VaIconVuestic,
      HeaderSelector,
      MessageDropdown,
      NotificationDropdown,
      LanguageDropdown,
      ProfileDropdown,
    },
    props: {
      minimized: {
        type: Boolean,
        required: true,
      },
    },
    data() {
      return {
        userName: 'Vasili S',
      }
    },
    computed: {
      minimizedProxy: {
        get() {
          return this.minimized
        },
        set(minimized) {
          this.$emit('update:minimized', minimized)
        },
      },
    },
  }
</script>

<style lang="scss">
    .va-navbar{
        height: 2rem!important;
        &__shape{
            height: 2rem!important;
        }
    }


    .app-navbar {
        &__button {
            width: 10.8125rem;
            .va-button__content__icon-left.fa-github {
                font-size: 1.5rem;
            }
        }
        &__actions {
            justify-content: flex-end;
        }
        &__mailto-link:hover {
            filter: brightness(85%);
        }
        @include media-breakpoint-down(lg) {
            &__button {
                display: none !important;
            }
            .profile-dropdown {
                margin: auto 0 auto .75rem;
            }
        }
        @include media-breakpoint-down(md) {
            @at-root .va-navbar__text {
                display: none !important;
            }
        }
        @include media-breakpoint-down(sm) {
            &__button {
                display: none !important;
            }
            &__actions {
                justify-content: space-around;
                padding: 0 .5rem;
            }
            .profile-dropdown {
                position: absolute;
                right: .75rem;
                top: 1.25rem;
                height: fit-content;
                margin: auto;
            }
        }
    }
</style>
